import React from 'react';
import { HashRouter as Router, Routes, Route, Link } from 'react-router-dom';

import HomePage from './features/home/pages/HomePage';
import TeamRosterPage from './features/lineup/pages/TeamRosterPage';
import DataTablePage from './features/data/DataTablePage'; // Now labeled "Nexus Beings"
import AddDropPage from './features/director/pages/AddDropPage';
import TradePage from './features/director/pages/TradePage.jsx';
import LoginPage from './features/auth/LoginPage';
import ProtectedRoute from './features/auth/ProtectedRoute';
import { AuthProvider } from './features/auth/AuthContext';
import DriverLicenseList from './features/data/DriverLicenseList';
import SimulationStatsPage from './features/simulation/SimulationStatsPage';

//  New imports
import DirectorAccessPage from './features/director/pages/DirectorAccessPage';
import DirectorDashboardPage from './features/director/pages/DirectorDashboardPage';

function App() {
  return (
    <AuthProvider>
      <Router>
        <div className="p-4">
          {/* Navigation Bar */}
          <nav className="mb-6 flex gap-4 items-center">
            <Link to="/" className="px-3 py-1 border rounded">Home</Link>
            <Link to="/access" className="px-3 py-1 border rounded">Director HQ</Link>
            <Link to="/data-table" className="px-3 py-1 border rounded">Nexus Beings</Link>
            <div className="relative group">
              <button className="px-3 py-1 border rounded">Transactions</button>
              <div className="absolute left-0 mt-1 hidden group-hover:block bg-white border rounded shadow-lg z-10">
                <Link to="/director/add-drop" className="block px-4 py-2 hover:bg-gray-100">Add/Drop</Link>
                <Link to="/director/trade" className="block px-4 py-2 hover:bg-gray-100">Trade</Link>
              </div>
            </div>
            <Link to="/simulation-stats" className="px-3 py-1 border rounded">Simulation Stats</Link>
            <Link to="/access" className="px-3 py-1 border rounded">Director Access</Link>
            <Link to="/login" className="px-3 py-1 border rounded">Login</Link>
          </nav>

          {/* Routes */}
          <Routes>
            <Route path="/" element={<HomePage />} />
            <Route path="/team-roster/:teamId" element={<TeamRosterPage />} />
            <Route path="/data-table" element={<DataTablePage />} />
            <Route
              path="/director/add-drop"
              element={
                <ProtectedRoute>
                  <AddDropPage />
                </ProtectedRoute>
              }
            />
            <Route
              path="/director/trade"
              element={
                <ProtectedRoute>
                  <TradePage />
                </ProtectedRoute>
              }
            />
            <Route path="/login" element={<LoginPage />} />
            <Route path="/driver-license" element={<DriverLicenseList />} />
            <Route path="/simulation-stats" element={<SimulationStatsPage />} />
            {/*  Director Routes */}
            <Route path="/access" element={<DirectorAccessPage />} />
            <Route path="/director-dashboard/:teamId" element={<DirectorDashboardPage />} />
          </Routes>
        </div>
      </Router>
    </AuthProvider>
  );
}

export default App;